body {
  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #4fc08d;
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.3;
}

h1,
h2 {
  margin-bottom: 16px;
  margin-bottom: 1rem;
}

h1 {
  font-size: 48px;
  font-size: 3rem;
  font-weight: 300;
}

.voletta {
  padding: 20px;
}

.voletta-body {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

header {
  margin-bottom: 20px;
}

header img {
  max-width: 100px;
}

.voletta-input {
  font-size: 32px;
  font-size: 2rem;
  margin-bottom: 20px;
}

.voletta-input input {
  padding: 16px 20px;
  font-size: 35.2px;
  font-size: 2.2rem;
  max-width: 80%;
  margin: 0 auto;
  border-radius: 4px;
  box-shadow: 0 0 4px rgb(79, 153, 120) inset;
  border: 2px solid rgb(79, 153, 120);
  outline: 0;
}

.voletta-bottom h2 {
  margin-top: 19.2px;
  margin-top: 1.2rem;
}

label {
  color: #4fc08d;
  border-radius: 4px;
  background-color: #fff;
  padding: 0 4px;
  display: inline-block;
  font-size: 22.4px;
  font-size: 1.4rem;
  vertical-align: middle;
  margin-bottom: 1px;
}

footer {
  margin-top: 80px;
  border-top: 1px solid #fff;
  padding: 20px;
  font-size: 16px;
  font-size: 1rem;
  opacity: .6;
}



